  constructor( private title: Title, private meta: Meta) {
    title.setTitle('Дом юриста - Договір підряду');
    meta.addTags([
      {name: 'key', content: 'юриспруденция, договір'},
      {name: 'description', content: 'Договір підряду'}
    ]);
  }

  ngOnInit() {
    this.onTabContent( 'first');
    this.modalIsOpen = false;
  }

  // Toggle tabs
  onTab(e) {
    if ( e === 'first') {
      this.classTab = e;
    }
    if ( e === 'second') {
      this.classTab = e;
    }
    if ( e === 'third') {
      this.classTab = e;
    }
    this.onTabContent(e);
  }

  onTabContent (e) {
    this.adviceTab.filter( tab => {
      tab.styleOption === e ? this.adviceTabTitle = tab.title : tab;
      tab.styleOption === e ? this.adviceTabText = tab.description : tab;
    });
  }

