import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule } from '@angular/forms';

// Material modules
import {
  MatBadgeModule,
  MatButtonModule,
  MatCardModule,
  MatFormFieldModule,
  MatIconModule,
  MatInputModule,
  MatToolbarModule
} from '@angular/material';


// Srvices
import { FooterService } from './services/footer.service';


// Components
import { HeaderComponent } from './components/header/header.component';
import { FooterComponent } from './components/footer/footer.component';
import { HomeComponent } from './components/pages/home/home.component';
import { CardItemComponent } from './components/card-item/card-item.component';
import { ItemBoxComponent } from './components/item-box/item-box.component';
import { MainNavComponent } from './components/main-nav/main-nav.component';
import { SearchComponent } from './components/search/search.component';
import { ConsultationComponent } from './components/pages/consultation/consultation.component';
import { SocialNetComponent } from './components/social-net/social-net.component';
import { ContractsComponent } from './components/pages/contracts/contracts.component';
import { RatingComponent } from './components/rating/rating.component';
import { TagsComponent } from './components/tags/tags.component';


@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    CardItemComponent,
    HomeComponent,
    ItemBoxComponent,
    MainNavComponent,
    SearchComponent,
    ConsultationComponent,
    SocialNetComponent,
    ContractsComponent,
    RatingComponent,
    TagsComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    FormsModule,
    MatIconModule,
    MatToolbarModule,
    MatButtonModule,
    MatBadgeModule,
    MatCardModule,
    MatFormFieldModule,
    MatInputModule
  ],
  providers: [FooterService],
  bootstrap: [AppComponent]
})
export class AppModule { }
